import React, { useState, useEffect } from 'react';
import { Link as RouterLink, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import validate from 'validate.js';
import { makeStyles } from '@material-ui/styles';
import {
  Grid,
  Button,
  IconButton,
  TextField,
  Link,
  Typography
} from '@material-ui/core';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import { useGlobal } from 'reactn';
import { useCookies } from 'react-cookie';
import Alert from '@material-ui/lab/Alert';
import axios from 'axios';
const API_URL = process.env.REACT_APP_SERVER;

const schema = {
  email: {
    presence: { allowEmpty: false, message: 'is required' },
    email: true,
    length: {
      maximum: 64
    }
  },
  password: {
    presence: { allowEmpty: false, message: 'is required' },
    length: {
      maximum: 128
    }
  }
};

const successes = {
  NONE: 0,
  SUCCESS: 1,
  FAIL: 2,
  DISABLED: 3
};
const showSuccess = succ => {
  if (succ !== successes.NONE) {
    let color = 'error';
    let text = 'Account Not Found! Please Try Different Email/Password';
    switch (succ) {
      case successes.SUCCESS:
        color = 'success';
        text = 'Success';
        break;
      case successes.DISABLED:
        color = 'warning';
        text = 'Disabled, Please Contact Webmaster';
        break;
    }
    return <Alert severity={color}>Login {text}</Alert>;
  }
};

const SignIn = props => {
  const { history } = props;

  const classes = useStyles();

  const [formState, setFormState] = useState({
    isValid: false,
    values: {},
    touched: {},
    errors: {}
  });
  const [cookies, setCookie] = useCookies(['login']);
  const [global, setGlobal] = useGlobal();
  const [isOpen, setOpen] = useState(false);
  const [loginSuccess, setLoginSuccess] = useState(successes.NONE);

  useEffect(() => {
    const errors = validate(formState.values, schema);

    setFormState(formState => ({
      ...formState,
      isValid: errors ? false : true,
      errors: errors || {}
    }));
  }, [formState.values]);

  const handleBack = () => {
    history.goBack();
  };

  const handleChange = event => {
    event.persist();

    setFormState(formState => ({
      ...formState,
      values: {
        ...formState.values,
        [event.target.name]:
          event.target.type === 'checkbox'
            ? event.target.checked
            : event.target.value
      },
      touched: {
        ...formState.touched,
        [event.target.name]: true
      }
    }));
  };

  const checkLogin = async () => {
    // MAKE THIS SECURE OMG
    await axios
      .post(`${API_URL}/people/login/`, {
        email: formState.values.email,
        passphrase: formState.values.password
      })
      .then(response => {
        if (response.data.length > 0) {
          let info = response.data[0];
          if (info.disabled === 0) {
            let details = {
              userId: info.user_id,
              name: info.firstname
            };
            setGlobal(details);
            let expr = new Date();
            expr.setMonth(expr.getMonth() + 1);
            setCookie('login', details, { expires: expr });
            setLoginSuccess(successes.SUCCESS);
            history.push('/');
          } else {
            setLoginSuccess(successes.DISABLED);
          }
        } else {
          setLoginSuccess(successes.FAIL);
        }
      });
  };

  const handleSignIn = event => {
    event.preventDefault();
    checkLogin();
  };

  const hasError = field =>
    formState.touched[field] && formState.errors[field] ? true : false;

  return (
    <div className={classes.root}>
      <Grid className={classes.grid} container>
        <Grid className={classes.content} item lg={12} xs={12}>
          {showSuccess(loginSuccess)}
          <div className={classes.content}>
            <div className={classes.contentHeader}>
              <IconButton onClick={handleBack}>
                <ArrowBackIcon />
              </IconButton>
            </div>
            <div className={classes.contentBody}>
              <form className={classes.form} onSubmit={handleSignIn}>
                <Typography className={classes.title} variant="h2">
                  Sign in
                </Typography>

                <TextField
                  className={classes.textField}
                  error={hasError('email')}
                  fullWidth
                  helperText={
                    hasError('email') ? formState.errors.email[0] : null
                  }
                  label="Email address"
                  name="email"
                  onChange={handleChange}
                  type="text"
                  value={formState.values.email || ''}
                  variant="outlined"
                />
                <TextField
                  className={classes.textField}
                  error={hasError('password')}
                  fullWidth
                  helperText={
                    hasError('password') ? formState.errors.password[0] : null
                  }
                  label="Password"
                  name="password"
                  onChange={handleChange}
                  type="password"
                  value={formState.values.password || ''}
                  variant="outlined"
                />
                <Button
                  className={classes.signInButton}
                  color="primary"
                  disabled={!formState.isValid}
                  fullWidth
                  size="large"
                  type="submit"
                  variant="contained">
                  Sign in now
                </Button>
                <Typography color="textSecondary" variant="body1">
                  Don't have an account?{' '}
                  <Link component={RouterLink} to="/sign-up" variant="h6">
                    Sign up
                  </Link>
                </Typography>
              </form>
            </div>
          </div>
        </Grid>
      </Grid>
    </div>
  );
};

SignIn.propTypes = {
  history: PropTypes.object
};

export default withRouter(SignIn);

